<template>
  <div>
    <b-card no-body style="border-top: none;">
      <b-card-body>
        <form @submit="editVault">
          <b-row>
            <b-form-group class="col-12" label="Username" label-for="elementsAppend">
              <b-input-group>
                <b-form-input type="text" v-model="form.user.username" disabled></b-form-input>
              </b-input-group>
            </b-form-group>
          </b-row>

          <hr />

          <b-row>
            <b-form-group class="col-6" label="total_withdrawals" label-for="elementsAppend">
              <b-input-group>
                <b-form-input disabled type="text" v-model="form.total_withdrawals"></b-form-input>
              </b-input-group>
            </b-form-group>

            <b-form-group class="col-6" label="total_rewards" label-for="elementsAppend">
              <b-input-group>
                <b-form-input disabled type="text" v-model="form.total_rewards"></b-form-input>
              </b-input-group>
            </b-form-group>
          </b-row>

          <hr />

          <b-row>
            <b-form-group class="col-12" label="Value" label-for="elementsAppend">
              <b-input-group>
                <b-form-input number type="text" v-model="form.amount"></b-form-input>
              </b-input-group>
            </b-form-group>
          </b-row>

          <hr />

          <div class="form-actions">
            <div v-if="this.edit_mode">
              <b-button v-if="!loading" type="submit" variant="primary mr-2">
                Save changes
                <i class="icon-cloud-upload" />
              </b-button>
              <b-button v-if="loading" variant="primary mr-2" disabled>
                Loading
                <i class="fa fa-spinner fa-spin" />
              </b-button>
              <b-button type="button" variant="danger" disabled>Delete</b-button>
            </div>
            <div v-else>
              <b-button v-if="!loading" type="submit" variant="warning mr-2">Create Now!</b-button>
              <b-button v-if="loading" variant="warning mr-2" disabled>
                Loading
                <i class="fa fa-spinner fa-spin" />
              </b-button>
              <b-button type="button" variant="secondary" @click="reset">Reset</b-button>
            </div>
          </div>
        </form>
      </b-card-body>
    </b-card>
  </div>
</template>

<script>
import _ from "lodash";

export default {
  name: "vaults-new",
  data() {
    return {
      user_uuid: false,
      edit_mode: false,
      loading: false,
      form: {
        parent: {},
        sponsor: {},
        user: {}
      }
    };
  },

  created() {
    if (this.$route.params.uuid) {
      this.edit_mode = true;
      this.point_uuid = this.$route.params.uuid;
      this.fetchVault(this.$route.params.uuid);
    }
  },

  methods: {
    fetchVault(uuid) {
      this.loading = true;
      this.$http("/api/v1/admin/vaults/" + uuid, {
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        }
      })
        .then(response => {
          console.log(response.data);
          this.form = response.data;
          this.loading = false;
        })
        .catch(error => {
          console.log(error.response);
          this.loading = false;
        });
    },

    editVault(evt) {
      evt.preventDefault();
      this.loading = true;
      this.$http({
        url: "/api/v1/admin/vaults/" + this.form.id,
        data: this.form,
        method: "PUT",
        headers: {
          "content-type": "application/json",
          Authorization: this.$store.getters.authToken
        }
      })
        .then(response => {
          this.$toasted.show("Vault successfully updated", {
            position: "top-right",
            duration: 5000,
            type: "default",
            closeOnSwipe: true
          });
          this.loading = false;
        })
        .catch(error => {
          error.response.data.map(m => {
            this.$toasted.show(m, {
              position: "top-right",
              duration: 5000,
              type: "error",
              closeOnSwipe: true
            });
            this.loading = false;
          });
        });
    },

    reset() {
      this.form = {
        publish: true
      };
    }
  }
};
</script>
